<template>
  <div>
    <div class="lds-ring" v-if="loader"><span id="loaderText">Loading</span><div></div><div></div><div></div><div></div></div>
    <!-- Career Section Start -->
    <b-container id="careerSection" @mouseover="careerHover(true)" @mouseout="careerHover(false)" v-if="dataLoadSignal">
      <b-row>
        <b-col cols="12" md="6">
          <div id="careerContent">
            <h1>Career Objective</h1>
            <div class="career-info">
              <p>
                I am passionate programmer (ACMICPC contestant) and possess sound knowledge of Javascript, Vue JS, Nuxt JS, Vuetify, React JS, JQuary, Python, PHP,
                DBMS(SQL Server, MySql), Version Control (git), HTML5, CSS3, Bootstrap, Stylus, Automation and web development methodology related fields.
              </p>
              <p>I want to apply my knowledge and skills to help achieve the business goal of the company.</p>
            </div>
          </div>
        </b-col>
        <b-col cols="12" md="6">
          <div id="careerContent">
            <div class="code">
<pre><code class="language-js">{
  "name": "Debbroto Sarkar",
  "occupation": "Web Developer",
  "available_for_hire:" true
}
</code></pre>
            </div>
          </div>
        </b-col>
      </b-row>
    </b-container>
    <!-- Career Section End -->
    <div v-if="dataLoadSignal">
      <h1 style="background-color: red; padding: 10px; text-align: center; color: #fff;">Development Starts : In Progress</h1>
    </div>
    <br><br><br><br>
  </div>
</template>

<script>
import { db } from '../firebase.js';
export default {
  data() {
    return {
      jobRetriveData: [],
      loader: true,
      dataLoadSignal: false
    }
  },
  firestore(){
    return {
      jobRetriveData: db.collection('jobs').orderBy('createdAt','asc')
    }
  },
  methods: {
    careerHover: function(value){
      if(value == true){
        this.$emit('career', true);
      }
      if(value == false){
        this.$emit('career', false);
      }
    },
    skillsHover: function(value){
      if(value == true){
        this.$emit('skills', true);
      }
      if(value == false){
        this.$emit('skills', false);
      }
    }
  },
  watch: {
    'jobRetriveData': function(){
      if(this.jobRetriveData.length > 1){
        this.loader = false;
        this.dataLoadSignal = true;
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
/** Career Section Start */
.code {
  padding-top: 70px;
}
/** Career Section End */
/** Page Media Query Start */
@media (min-width: 1200px){
  /** Career Section */
  #careerSection{
    margin-top: 70px;
    padding: 70px 0 80px;
  }
  #careerSection #careerContent{
    padding: 0 20px;
  }
}
@media screen and (max-width: 991px) {
  
}

@media screen and (max-width: 767px) {
  /** Career Section */
  pre{
    margin: 1.5rem 0 0;
  }
  .code {
    padding-top: 0rem;
  }
}

@media screen and (max-width: 479px) {
  /** Career Section */
  pre{
    margin: 1.5rem 0 0;
  }
  .code {
    padding-top: 0rem;
  }
}
/** Page Media Query End */
</style>



