<template>
  <div>
    <!-- Loader -->
    <div class="lds-ring" v-if="loader"><span id="loaderText">Loading</span><div></div><div></div><div></div><div></div></div>
    <!-- Career Section Start -->
    <b-container id="careerSection" @mouseover="careerHover(true)" @mouseout="careerHover(false)" v-if="dataLoadSignal">
      <b-row>
        <b-col cols="12" md="6">
          <div id="careerContent">
            <h1>Career Objective</h1>
            <div class="career-info">
              <p>
                I am passionate programmer (ACMICPC contestant) and possess sound knowledge of Javascript, Vue JS, Nuxt JS, Vuetify, React JS, JQuary, Python, PHP,
                DBMS(SQL Server, MySql), Version Control (git), HTML5, CSS3, Bootstrap, Stylus, Automation and web development methodology related fields.
              </p>
              <p>I want to apply my knowledge and skills to help achieve the business goal of the company.</p>
            </div>
            <router-link to="career">
              <a class="button">See more</a>
            </router-link>
          </div>
        </b-col>
        <b-col cols="12" md="6">
          <div id="careerContent">
            <div class="code">
<pre><code class="language-js">{
  "name": "Debbroto Sarkar",
  "occupation": "Web Developer",
  "available_for_hire:" true
}
</code></pre>
            </div>
          </div>
        </b-col>
      </b-row>
    </b-container>
    <!-- Career Section End -->

    <!-- Skill Section Start -->
    <div id="skillSection" @mouseover="skillsHover(true)" @mouseout="skillsHover(false)" v-if="dataLoadSignal">
      <b-container>
        <h1>Technical Skills</h1>
        <p>This comes from having a decent understanding of all the code as well as being able to read the Perl scripts.</p>
        <div class="skill-cards">
          <b-row v-for="row in formattedClubs" :key="row" class="skill-row">
            <b-col cols="12" sm="12" md="" lg="" v-for="club in row" :key="club">
              <vue-flip active-hover="true" class="card html-card">
                <div slot="front" class="slot">
                  <img :src="club.img">
                  <h2>{{ club.name }}</h2>
                </div>
                <div slot="back" class="slot">
                  <vue-circle
                    :progress="club.progress"
                    :size="100"
                    :reverse="false"
                    line-cap="round"
                    :fill="club.fill"
                    empty-fill="rgba(255, 255, 255, 1)"
                    :animation-start-value="0.0"
                    :start-angle="60"
                    insert-mode="append"
                    :thickness="5"
                    :show-percent="true"
                    @vue-circle-progress="progress"
                    @vue-circle-end="progress_end"
                    class="vue-circle"
                  >
                  </vue-circle>
                </div>
              </vue-flip>
            </b-col>
          </b-row>
        </div>
      </b-container>
    </div>
    <!-- Skill Section End -->
    <div v-if="dataLoadSignal">
      <h1 style="background-color: red; padding: 10px; text-align: center; color: #fff;">Development Starts : In Progress</h1>
    </div>
    <br><br><br><br>
  </div>
</template>
<script>
import { db } from '../firebase.js';
import Vue from 'vue';
import VueFlip from 'vue-flip';
import VueCircle from 'vue2-circle-progress';

export default {
  components: {
    'vue-flip': VueFlip,
    VueCircle
  },
  data() {
    return {
      jobRetriveData: [],
      loader: true,
      dataLoadSignal: true,
      fill : { gradient: ["#e34c25"] },
      skillDate: [
          { name:'HTML', progress: 80, fill: '#eb6d18', img: 'https://firebasestorage.googleapis.com/v0/b/portfolio-62af9.appspot.com/o/html.png?alt=media&token=50fbf3d7-569b-4d25-bca6-5bc772f40460'},
          { name:'CSS', progress: 80, fill: '#13c0e5', img: 'https://firebasestorage.googleapis.com/v0/b/portfolio-62af9.appspot.com/o/css.png?alt=media&token=bce4e715-4089-4a48-abc7-162a17f2e8e5'},
          { name:'JS', progress: 70, fill: '#f7af25', img: 'https://firebasestorage.googleapis.com/v0/b/portfolio-62af9.appspot.com/o/js.png?alt=media&token=3a67f7ef-1cfc-4c70-86bd-88f680d13ad6'},
          { name:'Vue JS', progress: 60, fill: { gradient: ["#000", "#41b883"] }, img: "https://firebasestorage.googleapis.com/v0/b/portfolio-62af9.appspot.com/o/vue.png?alt=media&token=ed1bdd96-2282-4db2-b289-312fcea11e1f"},
          { name:'Nuxt JS', progress: 40, fill: { gradient: ["#41b883", "#000000"] }, img: 'https://firebasestorage.googleapis.com/v0/b/portfolio-62af9.appspot.com/o/nuxt.png?alt=media&token=c58f3290-9474-45ae-b973-961dfdcf9e49'},
          { name:'JQuery', progress: 50, fill: { gradient: ["#82dea3"] }, img: 'https://firebasestorage.googleapis.com/v0/b/portfolio-62af9.appspot.com/o/jquery.png?alt=media&token=af06c3a4-9cba-486a-a226-703837b7ab4c'},
          { name:'Node JS', progress: 20, fill: { gradient: ["#99d815", "#554f0e"] }, img: 'https://firebasestorage.googleapis.com/v0/b/portfolio-62af9.appspot.com/o/node.png?alt=media&token=2484700e-0032-4e40-8353-3033a02360b6'},
          { name:'PHP', progress: 70, fill: { gradient: ["#ffd379", "#f73466"] }, img: 'https://firebasestorage.googleapis.com/v0/b/portfolio-62af9.appspot.com/o/php.png?alt=media&token=a5d6c832-8ebb-40e2-859f-896212a0f102'},
          { name:'Python', progress: 40, fill: { gradient: ["#3aeabb", "#fdd250"] }, img: 'https://firebasestorage.googleapis.com/v0/b/portfolio-62af9.appspot.com/o/python.png?alt=media&token=eae42f27-5a30-401c-826a-56d13675d509'},
          { name:'Bootstrap', progress: 80, fill: '#6633b0', img: 'https://firebasestorage.googleapis.com/v0/b/portfolio-62af9.appspot.com/o/bootstrap.png?alt=media&token=7d0a90e1-aca8-4412-a44b-1546f0fc04ce'},
          { name:'Vuetify', progress: 60, fill: { gradient: ["#6fbedb", "#10a8e5"] }, img: 'https://firebasestorage.googleapis.com/v0/b/portfolio-62af9.appspot.com/o/vuetify.png?alt=media&token=7e433eaa-42a9-406a-ac85-b539c56adc91'},
          { name:'MySql', progress: 70, fill: { gradient: ["#0d067c"] }, img: 'https://firebasestorage.googleapis.com/v0/b/portfolio-62af9.appspot.com/o/mysql.png?alt=media&token=b0f9cfe3-ee60-4674-b408-757aeddaf566'},
          { name:'Mongo DB', progress: 10, fill: { gradient: ["#00ff00", "#323b20"] }, img: 'https://firebasestorage.googleapis.com/v0/b/portfolio-62af9.appspot.com/o/mongo.png?alt=media&token=44d5bccc-3053-4045-95d9-a20f5e901627'},
          { name:'Firebase', progress: 50, fill: { gradient: ["#ffbd20", "#6fbedb"] }, img: 'https://firebasestorage.googleapis.com/v0/b/portfolio-62af9.appspot.com/o/firebase.png?alt=media&token=8a9ca77a-5bfd-480b-9a7c-0b1aa0dca09d'},
          { name:'Android', progress: 30, fill: { gradient: ["#3aeabb"] }, img: 'https://firebasestorage.googleapis.com/v0/b/portfolio-62af9.appspot.com/o/android.png?alt=media&token=bcc21420-ef61-4501-8815-a18520217bb5'}
      ]
    }
  },
  firestore(){
    return {
      jobRetriveData: db.collection('jobs').orderBy('createdAt','asc')
    }
  },
  methods: {
    careerHover: function(value){
      if(value == true){
        this.$emit('career', true);
      }
      if(value == false){
        this.$emit('career', false);
      }
    },
    skillsHover: function(value){
      if(value == true){
        this.$emit('skills', true);
      }
      if(value == false){
        this.$emit('skills', false);
      }
    },
    filter: function(key) {
      if (this.filterOption == key){
        key = null;
      }
      this.isotopeFilter(key);
      this.filterOption = key;
    },
    progress(event,progress,stepValue){
      console.log(stepValue);
    },
    progress_end(event){
      console.log("Circle progress end");
    }
  },
  computed: {
      formattedClubs() {
          return this.skillDate.reduce((c, n, i) => {
            if (i % 5 === 0) c.push([]);
            c[c.length - 1].push(n);
            return c;
          }, []);
      }
   },
  watch: {
    'jobRetriveData': function(){
      if(this.jobRetriveData.length > 1){
        this.loader = false;
        this.dataLoadSignal = true;
      }
    }
  }
}
</script>
<style scoped>
/** Page Media Query Start */
@media (min-width: 1200px){
  /** Career Section */
  #careerSection{
    margin-top: 70px;
    padding: 70px 0 80px;
  }
  #careerSection #careerContent{
    padding: 0 20px;
  }

  /** Skill Section */
  #skillSection{
    background-color: #f1f3f5;
    padding: 72px 0;
  }
  .skill-row{
    margin: 0 !important;
  }
}
@media screen and (max-width: 991px) {
  
}
@media screen and (max-width: 767px) {
  /** Career Section */
  pre{
    margin: 1.5rem 0 0;
  }
  .code {
    padding-top: 1.5rem;
  }

  /** Skill Section */
  .card{
    margin: .5rem 0rem;
  }
}
@media screen and (max-width: 479px) {
  /** Career Section */
  pre{
    margin: 1.5rem 0 0;
  }
  .code {
    padding-top: 1.5rem;
  }

  /** Skill Section */
  .card{
    margin: .5rem 0rem;
  }
}
/** Page Media Query End */
</style>



