<template>
  <div id="app">
    <!-- Navbar Start -->
    <b-navbar id="navbar" fixed="top">
      <b-container>
        <b-navbar-brand href="#" id="navbarBrand" to="/">
          debbroto 
          <vue-typed-js :strings="['<b>portfolio</b>']" :loop="true" :showCursor="false" id="navTypeJs">
            <span class="typing"></span>
          </vue-typed-js>
        </b-navbar-brand>
        <b-navbar-nav class="ml-auto" id="navbarRight">
          <b-nav-item to="career" id="careerLink"><b-spinner type="grow" label="Spinning" v-if="spiner['career']"></b-spinner>Career</b-nav-item>
          <b-nav-item to="skills" id="skillsLink"><b-spinner type="grow" label="Spinning" v-if="spiner['skills']"></b-spinner>Skills</b-nav-item>
          <b-nav-item to="portfolio" id="portfolioLink"><b-spinner type="grow" label="Spinning" v-if="spiner['portfolio']"></b-spinner>Portfolio</b-nav-item>
          <b-nav-item to="about" id="aboutLink"><b-spinner type="grow" label="Spinning" v-if="spiner['about']"></b-spinner>About</b-nav-item>
        </b-navbar-nav>
      </b-container> 
    </b-navbar>
    <!-- Navbar End -->
    <router-view v-on:career="careerHover" v-on:skills="skillsHover"/>
  </div>
</template>
<script>
import Vue from 'vue';
import VueTypedJs from 'vue-typed-js';
Vue.use(VueTypedJs);
export default {
  name: 'app',
  data: function(){
    return {
      spiner: {
        career: false,
        skills: false,
        portfolio: false,
        about: false
      },
    }
  },
  methods: {
    /******************** TODO SECTION START */
    /******************** Need Optimisation */
    careerHover: function(value){
      if(value == true){
        this.spiner['career'] = true;
      }
      if(value == false){
        this.spiner['career'] = false;
      }
    },
    skillsHover: function(value){
      if(value == true){
        this.spiner['skills'] = true;
      }else if(value == false){
        this.spiner['skills'] = false;
      }else{
        this.spiner['skills'] = true;
      }
    }
    /******************** TODO SECTION END */
  }
}
</script>
<style>
/** Global Fonts */
* {
  font-family: 'Montserrat', sans-serif;
}
/** Hide Scroll Bar */
::-webkit-scrollbar { 
  display: none; 
}

/** Header */
h1 {
  font-size: 2.5rem; 
}
h1,
h2,
h3,
h4,
h5 {
  line-height: 1.2;
  margin-top: 0;
  margin-bottom: 1.5rem;
  font-weight: 400;
  letter-spacing: -.03rem; 
  color:#444;
}

/** Navbar Design Start */
#navbar{
  background-color: #fff;
  padding: 20px 15px;
  border-bottom: 1px solid #d2d2d2;
}

#navbar #navbarBrand{
  text-transform: capitalize;
  font-size: 1rem;
  margin-left: 20px;
}
#navbarBrand #navTypeJs{
  display: inline-block;
}

#navbar #navbarRight{
  margin-right: 20px;
}
#navbarRight #careerLink, #portfolioLink, #skillsLink, #aboutLink{
  position: relative;
  margin-left: 15px;
  display: inline-block;
  font-size: 1rem;
  line-height: inherit;
  white-space: nowrap;
}
/** Spiner Custom */
.spinner-grow {
  position: absolute;
  left: -20px;
  top: 3px;
}
/** Navbar Design End */

/** Career Global Start */
#careerSection{
  margin-top: 35px;
  padding: 70px 15px 50px;
}
#careerContent .career-info{
  font-size: 1.1rem;
  color:#666;
  font-weight: inherit;
}
.code {
  margin: auto; 
  padding-top: 90px;
}
pre {
  background: #0D0D0D;
  color: #04ffa2;
  font-family: Menlo, Roboto Mono, Courier New, monospace;
  direction: ltr;
  text-align: left;
  white-space: pre;
  word-spacing: normal;
  word-break: normal;
  line-height: 1.6;
  font-weight: 400;
  -moz-tab-size: 2;
  -o-tab-size: 2;
  tab-size: 2;
  border-radius: 0;
  margin: 1.5rem 0rem 1.5rem 3rem;
  padding: 2rem;
  border-radius: 6px;
  border-top: 20px solid #d0d0d0;
  -webkit-font-smoothing: subpixel-antialiased;
  -webkit-box-shadow: 2px 3px 30px rgba(0, 0, 0, 0.15);
  box-shadow: 2px 3px 30px rgba(0, 0, 0, 0.15); 
}
code {
  font-family: Menlo, Roboto Mono, Courier New, monospace; 
  overflow: auto; 
}
a.button,
  [type=button],
  [type=submit] {
    display: inline-block;
    -webkit-appearance: none;
    -webkit-transition: background 500ms ease;
    transition: background 500ms ease;
    background-image: -webkit-gradient(linear, left top, right top, from(#33C7EF), color-stop(51%, #37F093), to(#33C7EF));
    background-image: linear-gradient(to right, #33C7EF 0%, #37F093 51%, #33C7EF 100%);
    background-size: 300% auto;
    color: white !important;
    border: none;
    border-radius: 6px;
    text-align: center;
    vertical-align: middle;
    padding: .75rem 1.50rem;
    font-size: 1.1rem;
    font-weight: 700;
    -webkit-box-shadow: 2px 3px 5px rgba(0, 0, 0, 0.05);
    box-shadow: 2px 3px 5px rgba(0, 0, 0, 0.05);
    cursor: pointer; }
    a.button:hover,
    [type=button]:hover,
    [type=submit]:hover {
      text-decoration: none;
      background-position: right center; 
    }
/** Career Global End */

/** Skill Global Start */
#skillSection{
  background-color: #f1f3f5;
  padding: 40px 0;
}
#skillSection h1{
  text-align: center;
}
#skillSection p{
  font-size: 1.2rem;
  color: #666;
  max-width: 700px;
  margin: 0 auto;
  text-align: center;
}
.skill-cards{
  margin-top: 30px;
}
.card {
  -webkit-transition: all 500ms ease;
  transition: all 500ms ease;
  -webkit-box-flex: 1;
  -ms-flex: 1;
  flex: 1;
  height: 200px;
  background: #fff;
  border: 1px solid #efefef;
  border-radius: 6px;
  -webkit-box-shadow: 0 6px 8px rgba(102, 119, 136, 0.03), 0 1px 2px rgba(102, 119, 136, 0.3);
  box-shadow: 0 6px 8px rgba(102, 119, 136, 0.03), 0 1px 2px rgba(102, 119, 136, 0.3);
  margin: .4rem -.5rem; 
}
.card:hover{
  background-color: #aaa;
}
.card img {
  display: block;
  margin: 0 auto; 
  height: 50px;
}
.card h2 {
  font-weight: 500;
  font-size: 1rem;
  margin: 2rem 0 0;
  text-align: center; 
}
.card p {
  color: #666;
  margin-bottom: 0; 
}
.front, .back{
  width: 100%;
}
.vue-circle{
  margin-top: -3px;
}
.slot{
  position: absolute;
  left: 50%;
  transform: translate(-50%, 50%);
}
.percent-text{
  font-weight: 200 !important;
  font-size: 18px !important;
  color: #fff !important;
}
/** Skill Global End */

/** App Media Only */
@media (min-width: 1200px){

  
}
@media screen and (max-width: 991px) {
  /** Navbar */
  #navbar #navbarRight{
    display: none;
    margin-right: 0;
  }
}
@media screen and (max-width: 767px) {
  /** Navbar */
  #navbar #navbarBrand{
    margin-left: 0;
  }
}
@media screen and (max-width: 479px) {

  
}
/** App Media End */

/** LOADER START */
/** Loader Custom */
.lds-ellipsis, .lds-ring{
  z-index: 1;
  position:absolute;
  left:0;
  right:0;
  margin-left:auto;
  margin-right:auto;
  top: 150px;
}

/** Loader Default */
.lds-ring {
  display: inline-block;
  width: 64px;
  height: 64px;
}
#loaderText{
  position: relative; 
  top: 100px; 
  color: #6c757d;
}
.lds-ring div {
  box-sizing: border-box;
  display: block;
  position: absolute;
  width: 51px;
  height: 51px;
  margin: 6px;
  border: 2px solid #6c757d;
  animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  border-color: #6c757d transparent transparent transparent;
}
.lds-ring div:nth-child(1) {
  animation-delay: -0.45s;
}
.lds-ring div:nth-child(2) {
  animation-delay: -0.3s;
}
.lds-ring div:nth-child(3) {
  animation-delay: -0.15s;
}
@keyframes lds-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
/** LOADER END */
</style>



