import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Career from '@/components/Career'
import Details from '@/components/Details'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/career',
      name: 'career',
      component: Career
    },
    {
      path: '/job_details',
      name: 'job_details',
      component: Details
    }
  ]
})



